.worker {
	-webkit-box-flex: 1;
	    -ms-flex: 1 1 calc(33% - 20px);
	        flex: 1 1 calc(33% - 20px);
	margin: 0 10px 40px;
	max-width: 350px;
	min-width: 250px;
	position: relative;
	border: 1px solid #333;
	border-radius: 4px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: 30px;
	text-align: center;
	z-index: 2;
}

.image {
	margin: 0 auto 20px;
	max-width: 250px;
	width: 100%;
}



